import { createApp } from 'vue'
import Router from './router/index'
import Store from './store/index'
import ElementPlus from 'element-plus'
import 'element-plus/lib/theme-chalk/index.css'
import App from './App.vue'
import ApiArr from './util/ajax'

async function getError(err, vm, info) {
  const componentData = await formatComponentData(vm)
  let {
    name,
    message, // 异常信息
    stack // 异常堆栈信息
  } = err
  if (stack) {
    const matchUrl = stack.match(/[(http)(https)]?:\/\/[^\n]+/)
    const urlFirstSTack = matchUrl && matchUrl.length ? matchUrl[0] : ''
    const regUrlCheck = /[(http)(https)]?:\/\/(\S)*\.js/

    let resourceUrl = ''
    if (regUrlCheck.test(urlFirstSTack)) {
      resourceUrl = urlFirstSTack.match(regUrlCheck)[0]
    }

    let stackCol = null
    let stackRow = null
    const posStack = urlFirstSTack.match(/:(\d+):(\d+)/)
    if (posStack && posStack.length >= 3) {
      [, stackCol, stackRow] = posStack
    }
    return {
      stack,
      message,
      info,
      col: Number(stackCol),
      row: Number(stackRow),
      resourceUrl,
      ...componentData,
      name,
      type: 'error'
    }
  }
}

async function formatComponentData(vm) {
  if (vm.$root === vm) return { root: 'root' };
  const { hostname, pathname, port, protocol, href } = window.location
  let componentName = 'anonymous component'
  let file = 'anonymous file'
  if (vm.$options) {
    componentName = vm.$options.name || vm.$options._componentTag || name
    file = vm.$options.__file || file
  }
  return {
    file,
    componentName,
    hostname,
    pathname,
    port,
    href,
    protocol
  }
}

createApp(App).use(Vue => {
  Vue.mixin({
    errorCaptured (err, vm, info) {
      getError(err, vm, info).then(res => {
        console.log('errorCaptured', res)
      })
      return false
    },
  })
  Vue.config.errorHandler = async function (err, vm, info) {
    getError(err, vm, info).then(res => {
      console.log('errorHandler', res)
    })
  }
}).use(ElementPlus).use(ApiArr).use(Store).use(Router).mount("#app")
