const isIP=/^(\d+)\.(\d+)\.(\d+)\.(\d+)$/;
const hostname = window.location.hostname
const port = window.location.port;
const hostnameArr = hostname.split('.')
let host = `https://hrss-api`
const setting = hostnameArr[hostnameArr.length - 3]
if (setting && (['uat', 'fat'].includes(setting) || setting.includes('testing') || setting.includes('dev'))) {
  host = `${host}.${setting}.wdeduc.com`
} else {
  host = `${host}.wdeduc.com`
}

if(isIP.test(hostname) || hostname.includes('localhost') || !!port){
  host = `https://hrss-api.testing1.wdeduc.com`;
}

export default {
  host
}