export default (axios) => ({
  getDetail ({ data, params } = {}) {
    return axios.Request({
      method: 'get', // 请求方式,不传默认get
      url: '/usercenter/v1/user/findById', // 请求路径，接口地址
      timeout: 5000, // 请求超时, 不传默认10秒
      headers: {}, // header体属性, 不传默认 任何请求方式都支持 body和url 传参
      data, // body参数, 格式 data: { key: value, ... }
      params // url参数，格式 params: { key: value, ... }
    })
  },
  findById (params) {
    return axios.GetByForm(`/usercenter/v1/findById`, params)
  }
})
