import API from '@api'

const ApiObj = require.context('./../modules/', true, /\.api\.js$/)

let ApiArr = {}
ApiObj.keys().forEach(key => {
  let urlArr = key.split("/");
  let apiKey=urlArr[urlArr.length-1].split(".")[0];
  ApiArr[apiKey] = ApiObj(key).default(API)
})

export default {
  install:  function (Vue) {
    Vue.config.globalProperties.$api = ApiArr
  }
}
