import Axios from 'axios'
// import { Message } from 'element-ui'
import host from '../config/url'

const baseURL = host.host
const errorArr = []
let errTimeout = null



const service = Axios.create({withCredentials: false, baseURL})

// 请求拦截器
service.interceptors.request.use(config => {
    // config.headers["token"] = sessionStorage.getItem("token") ? sessionStorage.getItem("token") : ""
    // config.headers["organ"] = sessionStorage.getItem("organ") ? sessionStorage.getItem("organ") : ""
    // config.headers["actor"] = sessionStorage.getItem("actor") ? sessionStorage.getItem("actor") : ""
    // config.headers["branch"] = sessionStorage.getItem("branch") ? sessionStorage.getItem("branch") : ""
    // sessionStorage.getItem("channel") && (config.headers["channel"] = sessionStorage.getItem("channel"))

    return config
}, err => {
    return Promise.reject(err)
})

// 响应拦截器
service.interceptors.response.use(config => {
    return config
}, err => {
    if(err.message.includes('timeout') || err.message.includes('Network Error')) {
        // Message.error(`网络异常，请重新尝试`)
    }
    return Promise.reject(err)
})

export function Request(config = {}) {
    const {
        url = '',
        data = null,
        params = null,
        headers = {
            "content-type": "application/json"
        },
        method = 'get',
        timeout = 30000
    } = config
    clearTimeout(errTimeout)
    errTimeout = setTimeout(() => {
        errorArr.forEach(item => {
            if (item.type) {
                item.type = 0
                // Message.error(item.val)
            }
        })
    }, 3000)
    return new Promise((resolve, reject) => {
        service({
            method, // 请求方式
            url, // 请求路径
            timeout, // 请求超时
            headers, // header体
            data, // body参数
            params //  URL参数
        }).then(res => {
            if (res.data.code && res.data.code!==50001) {
                if (errorArr.length) {
                    const isExists = errorArr.every(item => item.val === res.data.message)
                    !isExists && errorArr.push({
                        type: 1,
                        val: res.data.message
                    })
                } else {
                    errorArr.push({
                        type: 1,
                        val: res.data.message
                    })
                }
            }
            resolve({
                ...res.data
            })
        }).catch(err => {
            reject(err)
            return err
        })
    }).then(res => res)
}
